import Vue from 'vue';
//
import VueRouter from 'vue-router'
Vue.use(VueRouter);
//1.
import Index from '@/views/index'
import Home from '@/views/home'
import Login from '@/views/login'
import NotFound from '@/views/404'
import UpdatePassword from '@/views/update-password'
import pay from '@/views/pay'
import register from '@/views/register'
import center from '@/views/center'
    import news from '@/views/modules/news/list'
    import aboutus from '@/views/modules/aboutus/list'
    import yonghu from '@/views/modules/yonghu/list'
    import canzhuoyuding from '@/views/modules/canzhuoyuding/list'
    import canzhuoxinxi from '@/views/modules/canzhuoxinxi/list'
    import jinhuoxinxi from '@/views/modules/jinhuoxinxi/list'
    import orders from '@/views/modules/orders/list'
    import discusscaipinxinxi from '@/views/modules/discusscaipinxinxi/list'
    import caipinxinxi from '@/views/modules/caipinxinxi/list'
    import config from '@/views/modules/config/list'
    import caipinfenlei from '@/views/modules/caipinfenlei/list'
    import newstype from '@/views/modules/newstype/list'


//2.   
export const routes = [{
    path: '/',
    name: '',
    component: Index,
    children: [{
      // main
      path: '/',
      name: '',
      component: Home,
      meta: {icon:'', title:'center', affix: true}
    }, {
      path: '/updatePassword',
      name: '',
      component: UpdatePassword,
      meta: {icon:'', title:'updatePassword'}
    }, {
      path: '/pay',
      name: '',
      component: pay,
      meta: {icon:'', title:'pay'}
    }, {
      path: '/center',
      name: '',
      component: center,
      meta: {icon:'', title:'center'}
    }
      ,{
	path: '/news',
        name: '',
        component: news
      }
      ,{
	path: '/aboutus',
        name: '',
        component: aboutus
      }
      ,{
	path: '/yonghu',
        name: '',
        component: yonghu
      }
      ,{
	path: '/canzhuoyuding',
        name: '',
        component: canzhuoyuding
      }
      ,{
	path: '/canzhuoxinxi',
        name: '',
        component: canzhuoxinxi
      }
      ,{
	path: '/jinhuoxinxi',
        name: '',
        component: jinhuoxinxi
      }
      ,{
        path: '/orders/:status',
        name: '',
        component: orders
      }
      ,{
	path: '/discusscaipinxinxi',
        name: '',
        component: discusscaipinxinxi
      }
      ,{
	path: '/caipinxinxi',
        name: '',
        component: caipinxinxi
      }
      ,{
	path: '/config',
        name: '',
        component: config
      }
      ,{
	path: '/caipinfenlei',
        name: '',
        component: caipinfenlei
      }
      ,{
	path: '/newstype',
        name: '',
        component: newstype
      }
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {icon:'', title:'login'}
  },
  {
    path: '/register',
    name: 'register',
    component: register,
    meta: {icon:'', title:'register'}
  },
  {
    path: '*',
    component: NotFound
  }
]
//3.VueRouter  
const router = new VueRouter({
  mode: 'hash',
  /*hashhistory*/
  routes //  routes: routes
})
const originalPush = VueRouter.prototype.push
//push
VueRouter.prototype.push = function push(location) {
   return originalPush.call(this, location).catch(err => err)
}
export default router;
